export namespace RequestGPG {
    export class Request {
        method: Method = Method.Get;
        headers: Headers = new Headers();
        body: string
        setMethod(_method: Method) {
            this.method = _method;
            return this
        }
        setHeaders(_headers: Headers) {
            this.headers = _headers;
            return this
        }
        setToken(str: string) {
            this.headers.Authorization = str
            return this
        }
        setBody(_body: string) {
            this.body = _body;
            return this
        }
        async fetchData(_url: string, callback: Function) {
            return new Promise<void>((resolve, reject) => {
                fetch(_url, this)
                    .then(response => response.json())
                    .then(response => callback(response))
                    .then(resolve)
            })

        }

    }
    class Headers {

        [x: string]: string;
        "Content-Type": string = "application/json, text/plain, */*"
        "Accept": string = "application/json;charset=UTF-8"
        "Authorization": string = ""
    }

    export namespace Body {
        export class ValidateContactInfo_Phone {
            Phone: string
        }
        export class ValidateContactInfo_Email {
            Email: string
        }
        export class RankList {

        }
        export class MemberInfo {
            sign: string;
        }
    }
    export namespace ResponseDataMyInfo {
        export class MyInfo {
            Status: State = new State();
            data: Data = new Data()
        }
        class State {
            Code: string = "0"
            Message: string = "Success"
        }
        class Data {
            friendCount: number;
            introduceCode: string;
            isTrial: false;
            memberID: number;
            nickName: string;
            phoneNumber: string;
            photo: Photo;
            promotionCode;
            promotionUrl;
            registerSource: string;
            vip: VIP;
        }
        class Photo {
            frame: string;
            headPhoto: string;
        }
        class VIP {
            experience: number;
            level: number;
            nextExperience: number;
        }
    }
    export namespace ResponseDataValidateContactInfo {
        export class ValidateContactInfo {
            Status: State = new State();
        }
        class State {
            Code: string = "0"
            Message: string = "Success"
        }

    }
    export enum Method {
        Get = "Get",
        POST = "POST",
    }


}

